<template>
  <div :class="changeBackground()">
    <h1>{{ msg }}</h1>
    <button @click="change = !change">Change Background</button>
    <br />
    <div>
      <input v-model="name" type="text" />
      <input v-model="surname" type="text" />
      <input v-model="age" type="text" />
      <input v-model="email" type="text" />
      <button v-on:click="getForm">Press me</button>
    </div>

    <br />
    <div v-for="contact of contacts" :key="contact.name">
      <p>
        name: <b>{{ contacts.name }} <button @click="showMessage">??</button></b>
      </p>
      <p>
        surname: <b>{{ contacts.surname }}</b>
      </p>
      <p>
        age: <b>{{ contacts.age }}</b>
      </p>
      <p>
        email: <b>{{ contacts.email }}</b>
      </p>
    </div>

    <div>
      <button @click="show = !show">change this text</button>
      <p v-if="show">
        The last remnants of the human race left a dying Earth,<br />
        desperate to find a new home among the stars. Following in the footsteps
        of their ancestors,<br />
        they discover the greatest treasure of the past age—a world terraformed
        and prepared for human life.
      </p>
      <p v-else>
        Hello hello hello
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "Project_01",
  data() {
    return {
      name: " ",
      surname: " ",
      age: " ",
      email: " ",

      contacts: [],

      change: false,
      show: true
    };
  },
  methods: {
    getForm() {
      this.contacts.push({
        name: this.name,
        surname: this.surname,
        age: this.age,
        email: this.email
      });
    },
    changeBackground() {
      return {
        changed: this.change
      };
    },
    showMessage() {
      alert("Why are there no values?, i dont know")
    }
  },
  props: {
    msg: String
  }
};
</script>

<style>
div {
  background: white;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: black;
  background-color: white;
  margin-top: 60px;
}

.changed {
  background-color: green;
}
</style>
